<template>
  <div class="layout" style="padding: 20px;">
    <div class="layout-topbar">

    </div>
    <div class="layout-content">
      <Table :context="self" border :columns="columns" :data="datas"></Table>
      <div style="float: right;margin: 20px 0;">
        <Page :total="40" show-elevator show-sizer @on-change="changePage"></Page>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  
  export default {
      data() {
        return {
          self: this,
          columns: [
            {
              type: 'selection',
              width: 50,
              align: 'center'
            },
            {
              title: '任务编号',
              key: 'id',
              width: 100
            },
            {
              title: '标题',
              key: 'title',
              render (row) {
                return `<a href="#">${row.title}</a>`
              }
            },
            {
              title: '状态',
              key: 'status',
              width: 120
            },
            {
              title: '优先级',
              key: 'priority',
              align: 'center',
              width: 120
            },
            {
              title: '所属项目',
              key: 'project',
              align: 'center',
              width: 120
            },
            {
              title: '指派给',
              key: 'assign',
              align: 'center',
              width: 120
            },
            {
              title: '更新时间',
              key: 'retime',
              align: 'center',
              width: 120,
              render (row, column, index) {
                return `{{ formatDate(${row.retime}) }}`
              }
            }
          ],
          datas: this.demodata()
        }
      },
      methods: {
        demodata () {
          let data = []
          for (let i = 0; i < 10; i++) {
            data.push({
              id: '#' + Math.floor(Math.random() * 1000 + 1),
              title: '模拟任务数据' + Math.floor(Math.random() * 1000 + 1),
              status: Math.floor(Math.random() * 3 + 1),
              priority: Math.floor(Math.random() * 5 + 1),
              project: '模拟项目' + Math.floor(Math.random() * 10 + 1),
              assign: '用户' + Math.floor(Math.random() * 20 + 1),
              retime: new Date()
            })
          }
          console.log(data)
          return data
        },
        formatDate (date) {
          let a = new Date()
          console.log(date)
          console.log(a)

          return a.getFullYear()
        },
        changePage () {
          this.datas = this.demodata()
        }
      }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">

</style>



